/* 
    ===================================================
        Component to demonstrate state of joystick
    ===================================================
*/
.site-demonstration{
    padding: 10px;
}

.demonstration-joystick-info{
    padding: 30px;
    font-size: 1.4rem;
}

.demonstration-data{
    display: flex;
    justify-content: space-around;
    padding: 15px;
}

.demonstration-data p span{
    color: #ce331f;
}

.links-source-code{
    color: #ce723b;
    text-decoration: none;
    transition: all .5s ease-in-out;
}

a.links-source-code:hover{
    text-shadow: 0 2px 20px rgba(0,0,0,.40);
    color: #f77327;
}

.try-it-title{
    padding-top: 30px;
}

@media (min-width: 768px){
    .demonstration-joystick-info{
        width: 60%;
        float: left;
    }
    .demosntration-canvas-div{
        width: 40%;
        float: left;
    }
}

canvas{
    width: 100%;
    box-sizing: content-box;
    height: 300px;
}

.implementation-title{
    text-align: center;
    font-size: 2.7rem;
}
