.title h2{
    text-align: center;
    color: #ff5b5b;
    font-style: italic;
    margin: 0;
    font-size: 2rem;
}

.title{
    position: relative;
    background:white;
    padding-bottom:4px;
    box-sizing: border-box;
    margin: auto;
}

.title:after{
    content: '';
    position: absolute;
    top: 0; right: 0; bottom: 0; left: 0;
    z-index: -1;
    margin-bottom: -3px;
    background: linear-gradient(0.25turn, rgb(251, 132, 63), rgb(252, 74, 50), rgba(199, 32, 32, 0.315));
}

.dis-flex{
    display: flex;
    margin: 10px 0;
}