/* 
    =======================
        Home page Header
    =======================
*/

.logo-title img{
    width: 100%;
    max-width: 150px;
    min-width: 50px;
    height: auto;
    padding: 10px;
}

.logo-title h1{
    margin: revert;
    color: #ff5b5b;
    text-shadow: 0 2px 20px rgba(0,0,0,.30);
}

.get-started-btn{
    display: block;
    margin: auto;
    width: 100px;
}

.homepage-header{
    background-color: #444444;
    border-radius: 0;
    box-shadow: 5px 4px 29px -9px #000000;
}

.background-navbar{
    background-color: #1f1f1f;
}

.white-toggler{
    filter: invert(100%) sepia(100%) saturate(1%) hue-rotate(137deg) brightness(103%) contrast(101%);
}

/* 
    =============================
        Site features Homepage
    =============================

*/
.single-feature img{
    max-height: 150px;
    width: auto;
    display: block;
    margin-left: auto;
    margin-right: auto;
}
.site-features{
    padding: 60px 0;
    margin: 0;
}

.single-feature h3{
    font-size: 1.3rem;
    padding: 15px 0;
    text-align: center;
    color: #da0404;
    text-shadow: 0 2px 20px rgba(0,0,0,.40);
}

.site-features-container{
    margin: 0 auto;
    max-width: 1600px;
    padding: 30px 0;
}

.single-feature{
    padding: 30px 0;
}


/* 
    ===========================
        Examples - Homepage
    ===========================
*/
.project-examples{
    min-height: 200px;
    border-radius: 5%;
    background-color: #ff5b5b;
    padding: 20px;
    margin: 20px;
}
.project-examples-section{
    padding-top: 40px;
}


/* 
    =======================
            Footer
    =======================
*/
.footer-docs{
    text-align: center;
}

.site-footer{
    background-color: #444444;
    padding: 20px;
    box-shadow: 5px 4px 29px -9px #000000;

}

a.docs-link, .contributor{
    font-size: 1.8rem;
    font-weight: bold;
    color: #ff5b5b;
}

.links-not-working span{
    padding: 5px;
    background-color: #ff5b5b;
    border-radius: 10px;
    display: none;
}

.links-not-working:hover{
    cursor: pointer;
}

.links-not-working:hover span{
    display: inline;
}

.site-projects{
    padding: 50px 0 90px 0;
}

.copyright{
    color: white;
}

/* 
    ======================
            Labels 
    ======================
    
*/
.page-label{
    padding: 30px 0;
    margin-bottom: 50px;
    text-align: center;
}


/* 
    ===============================
            Documentation
    ===============================
*/

.feature-explanation{
    padding: 20px 0;
}


.links-documentation{
    background-color: #4444444f;
    text-align: center;
    box-shadow: 5px 4px 29px -9px #0000004d;
    padding-bottom: 30px;
}

.links-documentation p{
    font-size: 20px;
}

.site-features-documentation{
    padding: 0 50px 50px 10%;
}

.useful-links{
    padding-bottom: 0;
}

/* 
    ===========================
            Examples Page
    ===========================

*/

.site-examples{
    padding: 40px 0;
}

.note-comment{
    margin: 0;
    color: #ff5b5b;
}


/*
    ===========================
            RESPONSIVENESS
    ===========================

*/

@media (min-width: 768px){

    .page-label{
        text-align: left;
    }
    
    .background-features{
        box-shadow: 5px 4px 29px -9px #0000004d;
    }
    .logo-title h1{
        font-size: 7vh;
    }

    .single-feature{
        padding: 10px;
    }

    .homepage-header{
        padding: 8rem 2rem;
    }

    .footer-docs{
        padding-left: 70px;
        text-align: left;
    }

    .links-documentation{
        text-align: left;
    }

    .useful-links{
        padding-bottom: 20px;
    }

    .small-page-footer {
        position: absolute;
        bottom: 0;
        width: 100%;
    }
}